/* eslint-disable react/prop-types */
import React from 'react'; // eslint-disable-line
import Link from 'gatsby-link';

export default ({ children }) => (
  <div>
    <header>
      <h1>React Drip Form</h1>

      <nav>
        <Link to="/">Home</Link>
        {' '}
        <Link to="/test">Test</Link>
      </nav>
    </header>

    <div>
      {children()}
    </div>

    <footer>
      <p>footer</p>
    </footer>
  </div>
);



